<template>
    <view class="photo-header">
        <i class="w_editor-icon w_editor-icon-add-fill" @click="activationAdd"></i>
    </view>
</template>

<script>
    export default {
        props: {
            value: {
              type: Object,
              default: undefined,
            },
        },
        methods: {
            activationAdd() {
                const obj = JSON.parse(JSON.stringify(this.value))
                const date = new Date()
                obj.activation = date.getMinutes() + ':' + date.getSeconds()
                this.$emit('input', obj)
            },
        },
    }
</script>

<style lang="scss" scoped>
    
    
    .photo-header{
        display: flex;
        align-items: center;
        .w_editor-icon-add-fill{
            font-size: 50rpx;
            margin-right: 10rpx;
        }
    }
</style>